<template>
        <section>
            <div class="my-popups">
                <div class="modal d-block ">
                    <div class="modal-dialog modal-dialog-centered ui-draggable">
                        <div class="modal-content">
                            <div class="modal-header ui-draggable-handle  py-0">
                                <h4 name="header" class="modal-title mt-2 pt-1">
                                    {{ $t('Comment') }}
                                </h4>
                                <button class="close" v-on:click="closePopup"><em aria-hidden="true" class="fa fa-times"></em>
                                </button>
                            </div>
                        <div class="listing py-3 px-3">
                                <dynamic-form lang="en" ref="PostCommentForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
     </section>  
</template>

<script>
import DataService from '@/services/DataService';

export default {
    props: {
        selectedRecordValues: {
            type: Array,
            required: true
        },
        AssignDpt: {
            
            type: Array,
            required: true
        }
    },
    props:{
        weekDateRange:{
            required:false
        }
    },
    data() {
        return {
            FormSchema: [{
                    layoutType: "double",
                    Data: [{
                            astype: "TextField",
                            label: this.$t('Title'),
                            name: "Title",
                            value: "",
                            placeholder: "",
                            config: {
                                //onChange: this.checkRuleName,
                            },
                            validationRules: { "required": true, max: 250 },
                        },
                        {
                        astype: "RadioField",
                            label: this.$t('Visibilty'),
                            name: "Visibilty",
                            value: 1,
                            placeholder: "",
                            visibility: true,
                            validationRules: {
                                "required": false
                            },
                            config: {
                                options: [{ value: 1, name: "Public"},{value: 0, name: "Private" }],
                               // onChange: this.ManageLimtedDuration,
                            }
                        }
                    ]
                },
                {
                    layoutType: "single",
                Data: [{
                    astype: "TextAreaField",
                    label: this.$t('Description'),
                    name: "description",
                    value: "",
                    mode: "tag",
                    placeholder: "",
                    config: {
                        options: [],
                    },
                    validationRules: {
                        "required": true
                    },
                }]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-send pr-2'></i>Post New Comment",
                    isVisible: true,
                },
            ],
            trackId:0,
            isLoading:false,
          //  isAddAssignDept:false
        }
    },
    created: function () {
       
    },
    methods: {
           closePopup() {
            var vm = this;
            vm.$parent.CloseComment();
        },
        onSubmit: function (formfields) {
            debugger;
            var vm = this; 
             var url = `weekDaterange=${vm.weekDateRange}&title=${formfields.Title}&description=${formfields.description}&access=${formfields.Visibilty}&statusCode=`;
             DataService.AddTimesheetComment(url).then(response => {
                if (response.data == true) {
                   this.$router.push({
                      name: 'ApprovalRule'
                  });
              vm.ShowAlert("Comment SuccessFully added ", 'success', true, vm.$t('Alert'));
             }
            })
        },
        }
}
</script>
