<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" :showCheckBox="true" :listType="[DetailListType]" ModuleName="Approval" 
            SubModuleCode="Approval" IdentityColumn="GROUPID" :HeaderText="$t('TimesheetApprovals')" :ListData="TimesheetApprovalList"
            :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">

        


            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'COMMENTSCOUNT'">
                    <a  data-toggle="modal" data-target="#tsacomment" data-toggle-tooltip="tooltip" data-original-title="Comments(0)"  v-on:click="AddCommentPopUp(data.row)">
                        <i class="fa fa-comment-o text-dark action-icon"></i>
                        <span class="noti-circle noti-blue text-center">{{data.row.COMMENTSCOUNT}}</span></a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'View'">
                    <a v-on:click="GeneralTimesheetPopup(data.row)" href="javascript:;"><em class="fa fa-eye text-info action-icon"></em></a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'STATUS_CODE'">
                    <span >{{data.row.STATUS_CODE ==  'TRACK_ENTRY_REJECTED'? 'Rejected' : data.row.STATUS_CODE == 'TRACK_ENTRY_ADD_MANUAL_PENDING' ? 'Pending': 'Approved' }} </span>
                </template>
            </template>
        </tg-list>
        <AddComment v-if="openModal" :weekDateRange="weekDateRange"></AddComment>
        <div class="my-popups" v-if="openGeneralTimesheetModal">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle  py-0">
                            <h4 name="header" class="modal-title mt-2 pt-1">
                                {{ $t('GeneralTimesheet') }}
                            </h4>
                            <button class="close" v-on:click="CloseComment"><em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                    
                    <div class="modal-body">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 searchpanel tablehead search float-left">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left py-2 p-0">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                <div class="row mt-0">
                                    <div class="col-sm-12 col-md-6 col-lg-5 col-xl-5 float-left pr-1 pl-0 mb-1">
                                        <div class="col-12 input-group timesheetdatepicker bg-white border-0 p-0">
                                            <div class="form-control">
                                                <label>Date Range:</label>&nbsp;<a id="aDateLable" class="pr-2 text-dark" style="cursor:default">{{ weekDateRange }}</a>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left pr-1 pl-0 mb-1">
                                        <div class="form-group dpddown">
                                            <select id="ddlUser"  class="form-control" name="Users"><option value="">Select User</option>
                                            <option  v-for="option in UserData" :value="option.value">{{ option.name }}</option>                                                    
                                        </select> 
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-4 col-lg-3 col-xl-3 float-left pr-1 pl-0 mb-1">
                                        <div class="search-btm-btn background-0 text-left form-check-inline">
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="listing">
                            <div class="col-12 mb-2">
                                    <div class="mr-2 d-inline-block">
                                        <span class="legends-purple colorboxsquare"></span>Automatic
                                    </div>
                                    <div class="mr-2 d-inline-block">
                                        <span class="legends_low colorboxsquare"></span>Manual
                                    </div>
                                    <div class="mr-2 d-inline-block">
                                        <span class="legends_medium colorboxsquare"></span>Timesheet
                                    </div>
                                    <div class="mr-2 d-inline-block">
                                        <span class="legends_profile colorboxsquare"></span>Leave
                                    </div>
                                    <div class="mr-2 d-inline-block">
                                        <span class="legends_high colorboxsquare"></span>Holiday
                                    </div>
                                    <div class="mr-2 d-inline-block">
                                        <span class="lg-pausedresumed colorboxsquare"></span>Paused/Resumed
                                    </div>
                            </div>
                        </div>
            </div>
                    </div>
                </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import Schema from '../../views/TimesheetApproval/TimesheetApprovalSchema.js'
import DataService from '../../services/DataService';
import AddComment from '../../components/Modal/Timesheet/AddComment.vue';
export default {
    components: {
        AddComment,
        },
    data() {
        return {
            widgets:[],
            openModal: false,
            isLoading: false,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            listheaderbuttons: Schema.buttons,
            LegendArray: [],
            TimesheetApprovalList: [],
            Headers: [{
                    "COLUMN_NAME": 'USER_NAME',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('UserName'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": 'WEEK_DATE_RANGE',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('TimesheetPeriod'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": 'TRACK_HOURS',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('TrackedHour'),
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": 'BREAK_DURATION',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('BreakHour'),
                    "DISPLAY_ORDER": 4,
                },
                {
                    "COLUMN_NAME": 'TRACK_HOURS',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('FinalTrackedHour'),
                    "DISPLAY_ORDER": 5,
                },
                {
                    "COLUMN_NAME": '',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('PerDiem'),
                    "DISPLAY_ORDER": 6,
                },
                {
                    "COLUMN_NAME": 'BONUS_AMOUNT',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('BonusAmount'),
                    "DISPLAY_ORDER": 7,
                },
                {
                    "COLUMN_NAME": 'STATUS_CODE',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Status'),
                    "DISPLAY_ORDER": 8,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": 'COMMENTSCOUNT',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Comment'),
                    "DISPLAY_ORDER": 9,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": 'View',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('View'),
                    "DISPLAY_ORDER": 10,
                    settings: {
                        isInSlot: true,
                    }
                },
            ],
            noRecord: true,
            SortBy: '',
            SortExp: '',
            GroupData: [],
            TotalRecords: 0,
            // PageNumber: 1,
            // PageSize: 10,
            CurrentPage: 1,
            TotalPages: 0,
            SearchuserId: '',
            userType: 'CA',
            DateTime: '',
            showData: '',
            statusCode: '',
            sortExp: '',
            sortBy: '',
            pageSize: 10,
            pageNumber: 1,
            projectId: '',
            DetailListType: 'List',
            openGeneralTimesheetModal:false,
            weekDateRange:'',
            UserData:[]
            
        };
    },
    created() {
        var vm = this;
        Schema.bindleftCommonSearchdropdown(this, "CA", "USERS");
        vm.FetchData(); 
        vm.LegendArray = [{
            class: 'legend-pending colorboxsquare',
            text: ('Auditable Records')
        },
        {
            class: 'legends_profile colorboxsquare',
            text: ('Unpaid Leave Records ')
        },
        {
            text: ('| The total break hours are shown only if the break is non-billable.')
        },
        ]
    },
    methods: {
        GetUser:function(){
            var vm=this;
            DataService.GetUsersByCompanyId().then(response=>{
            debugger;
            response.data.forEach(item=>{
            let obj1={
                name:item.username,
                value:item.user_id
                }
                // vm.leftSearchFields[2].listOptions.push(obj1);
                vm.UserData.push(obj1);
            })
        }).catch(err =>{
            console.log(err);
        })
        },
        pagerMethod: function (value) {
            debugger
            this.pageSize = value.PageSize;
            this.pageNumber = value.PageNumber;
            this.FetchData();
        },
        async FetchData() {
            debugger;
            var vueObj = this;
            vueObj.isLoading = true;
            //long companyId, long SearchuserId, string userType, DateTime? dtFrom, DateTime? dtTo, long? userId, string showData, string statusCode, string sortBy, string sortExp, int pageSize, int? pageNum, long? projectId
            var url = `userType=${vueObj.userType}&DateTime=${vueObj.DateTime}&showData=${vueObj.showData}&statusCode=${vueObj.statusCode}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNumber}&projectId=${vueObj.projectId}&${vueObj.searchCondition}`;
            await DataService.TimesheetApprovalList(url).then(response => {
                debugger;
                if (response.data.length > 0) {
                    vueObj.TimesheetApprovalList = response.data;
                    vueObj.TimesheetApprovalList.forEach(item=>{
                        var getitem = vueObj.TimesheetApprovalList.find(x=> x.TRACK_HOURS === item.TRACK_HOURS);
                        getitem.TRACK_HOURS = vueObj.secondsToTime(item.TRACK_HOURS);
                    })
                    vueObj.isLoading = false;
                    vueObj.TotalRecords = response.data[0].TotalRecords;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                    vueObj.CurrentPage = vueObj.pageNumber;
                    vueObj.noRecord = false;
                    vueObj.GetUser();
                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                }
                else {
                    vueObj.TimesheetApprovalList = [];
                }
                vueObj.isLoading = false;
            });
            setTimeout(function () {
                vueObj.CheckBoxBootstrap();
            }, 100);
        },
        
        ViewHistory() {
            debugger;
            this.$router.push({
                name: 'ApprovalHistory'
            });
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "ViewHistory":
                    this.ViewHistory();
                    break;
            }
        },
        AddCommentPopUp(data) {
            debugger;
                 
            this.openModal = true;
            this.weekDateRange=data.WEEK_DATE_RANGE;
                  
                // this.openModal = true;
            },
            CloseComment(){
                this.openGeneralTimesheetModal=false;
                this.openModal = false;
            },
            GeneralTimesheetPopup(data){
                debugger;
                this.openGeneralTimesheetModal = true;
                this.weekDateRange=data.WEEK_DATE_RANGE;
            },
            GeneralTimesheetClosePopup(){
                this.openGeneralTimesheetModal = false;
            },
        actionButtonClick(callbackfunction, item) { },
        renderActions(row) { },
        
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
       
        secondsToTime(secs){
            debugger;
            // TO Get Hours
            var hours = Math.floor(secs / (60 * 60));
            // To Get Minutes 
            var divisor_for_minutes = secs % (60 * 60);
            var minutes = Math.floor(divisor_for_minutes / 60);
            // To Get Seconds
            var divisor_for_seconds = divisor_for_minutes % 60;
            var seconds = Math.ceil(divisor_for_seconds);

            var obj = hours + ':' + minutes ;
            return obj;
        },
        ApproveTimesheet(){
           
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  "), function () {
                    var postJSON = {
                        ids: val,
                        tblname: "TALYGEN_APPROVAL_RULE",
                        primarycolname: "APPROVAL_RULE_ID"
                    };
                    vm.isLoading = true;
                    var url = `ids=${val}`
                    DataService.DeleteRule(url).then(response => {
                        if (response.data) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Approval')]), "Success", true, vm.$t('Alert'));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Group_Item')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.isLoading = false;
                    });
                });
            
         }
        }
    },
    components: { AddComment }
}
</script>
